import React, { useEffect, useState } from 'react';
import {
  DataGrid,
  IconButton,
  MoreIcon,
  theme,
} from '@daimler/react-ux-business-app';

import { columns } from '@domains/goods-movements/components/Content/constants';

import { Header } from './Header';

import { PageWrapper, PageHeader, PageContent } from './styled';
import { Path } from '@router/types';
import { GoodsMovementDataType } from './components/Content/types';
import { StyledCheckIcon } from '@domains/shared/styled';
import {
  AccountingType,
  GoodsMovementSearchRequest,
  SearchGoodsMovementItem,
} from '@api/generated/apiClient.generated';
import { apiClient } from '@api/client';
import { generatePath, useNavigate } from 'react-router-dom';

type GoodsMovementsDomainProps = {};

export const GoodsMovementsDomain: React.FC<GoodsMovementsDomainProps> = () => {
  const navigate = useNavigate();

  const [goodsMovements, setGoodsMovements] = useState<
    SearchGoodsMovementItem[]
  >([]);
  const [filter, setFilter] = useState<GoodsMovementSearchRequest>({});

  useEffect(() => {
    apiClient
      .goodsMovementsSearch(filter)
      .then((r) => setGoodsMovements(r.items ?? []));
  }, [filter]);

  const items = goodsMovements.map((goodsMovement) => {
    const jsonFilter = JSON.stringify(filter)?.replaceAll('*', '**');

    return {
      ...goodsMovement,
      key: goodsMovement.id,
      action: (
        <IconButton
          icon={<MoreIcon fill={theme.colors.petrol} />}
          onClick={() => {
            const path = !filter
              ? generatePath(Path.GOODSMOVEMENT, {
                  id: `${goodsMovement.id}`,
                })
              : generatePath(Path.GOODSMOVEMENTWITHFILTER, {
                  id: `${goodsMovement.id}`,
                  filter: `${jsonFilter}`,
                });
            navigate(path);
          }}
        />
      ),
      failed: goodsMovement.isFailed && <StyledCheckIcon />,
    } as GoodsMovementDataType;
  });

  const onSearch = (filter: GoodsMovementSearchRequest) => {
    setFilter(filter);
  };

  return (
    <PageWrapper>
      <PageHeader>
        <Header onSearch={onSearch} />
      </PageHeader>
      <PageContent>
        <DataGrid
          columns={columns}
          dataSource={items ?? []}
          pagination={false}
        />
      </PageContent>
    </PageWrapper>
  );
};
