import { Label, StyledInput } from './styled';

import {
  ClearButton,
  LabelText,
  RightSection,
  SearchButton,
  Wrapper,
} from '../../styled';
import { CloseIcon, SearchIcon, Select } from '@daimler/react-ux-business-app';
import React, { useState } from 'react';
import { Trans, useTranslation } from '@i10n/index';
import { AccountingType2 as AccountingType, GoodsMovementSearchRequest } from '@api/generated/apiClient.generated';

export type GoodsMovementsFiltersProps = {
  onSearch: (filter: GoodsMovementSearchRequest) => void;
};

export const Filters: React.FC<GoodsMovementsFiltersProps> = ({ onSearch }) => {
  const [finOrVin, setFinOrVin] = useState<string>();
  const [licensePlate, setLicensePlate] = useState<string>();
  const [partNumber, setPartNumber] = useState<string>();
  const [providerAccountingType, setProviderAccountingType] = useState<
  AccountingType | string
>('');
const [providerAccountingData, setProviderAccountingData] =
  useState<string>();
const [consumerAccountingType, setCustomerAccountingType] = useState<
  AccountingType | string
>('');
const [consumerAccountingData, setCustomerAccountingData] =
  useState<string>();
  const [isFailed, setIsFailed] = useState<boolean | string>('');

  const handleSearch = () => {
    const filter = {
      isFailed,
      vehicleIdentity: finOrVin,
      licensePlate,
      serviceProviderAccountingType: providerAccountingType,
      serviceConsumerAccountingType: consumerAccountingType,
      serviceProviderAccountingData: providerAccountingData,
      serviceConsumerAccountingData: consumerAccountingData,
      partNumber,
    } as GoodsMovementSearchRequest;

    onSearch(filter);
  };

  const handleClear = () => {
    setFinOrVin(undefined);
    setPartNumber(undefined);
    setCustomerAccountingData(undefined);
    setCustomerAccountingType('');
    setProviderAccountingData(undefined);
    setProviderAccountingType('');
    setIsFailed('');
    setLicensePlate(undefined);

    onSearch({} as GoodsMovementSearchRequest);
  };

  const t = useTranslation();

  const accountingTypes = Object.keys(AccountingType).map((key) => ({
    label: t(`Rabs.Invoices.Filter.AccountingTypes.${key}`),
    value: key,
  }));
  accountingTypes.unshift({ label: t('Rabs.Common.All'), value: '' });

  const isFailedOptions = [
    { label: t('Rabs.Common.All'), value: '' },
    { label: t('Rabs.Common.Yes'), value: 'true' },
    { label: t('Rabs.Common.No'), value: 'false' },
  ];

  return (
    <Wrapper>
      <Label>
        <LabelText>{t('Rabs.GoodsMovements.Filter.FinOrVin')}</LabelText>
        <StyledInput
          value={finOrVin}
          onChange={(e) => setFinOrVin(e.target.value)}
        />
      </Label>
      <Label>
        <LabelText>{t('Rabs.GoodsMovements.Filter.LicensePlate')}</LabelText>
        <StyledInput
          value={licensePlate}
          onChange={(e) => setLicensePlate(e.target.value)}
        />
      </Label>
      <Label>
        <LabelText>{t('Rabs.GoodsMovements.Filter.PartNumber')}</LabelText>
        <StyledInput
          value={partNumber}
          onChange={(e) => setPartNumber(e.target.value)}
        />
      </Label>
      <Label>
        <LabelText>
          {t('Rabs.Invoices.Filter.ProviderAccountingType')}
        </LabelText>
        <Select
          options={accountingTypes}
          value={providerAccountingType}
          onSelect={(value) => setProviderAccountingType(value)}
          placeholder=""
        />
      </Label>
      <Label>
        <LabelText>
          {t('Rabs.Invoices.Filter.ProviderAccountingData')}
        </LabelText>
        <StyledInput
          value={providerAccountingData}
          onChange={(e) => setProviderAccountingData(e.target.value)}
        />
      </Label>
      <Label>
        <LabelText>
          {t('Rabs.Invoices.Filter.CustomerAccountingType')}
        </LabelText>
        <Select
          options={accountingTypes}
          value={consumerAccountingType}
          onSelect={(value) => setCustomerAccountingType(value)}
          placeholder=""
        />
      </Label>
      <Label>
        <LabelText>
          {t('Rabs.Invoices.Filter.CustomerAccountingData')}
        </LabelText>
        <StyledInput
          value={consumerAccountingData}
          onChange={(e) => setCustomerAccountingData(e.target.value)}
        />
      </Label>
      <Label>
        <LabelText>{t('Rabs.GoodsMovements.Filter.IsFailed')}</LabelText>
        <Select
          options={isFailedOptions}
          value={isFailed == undefined ? '' : isFailed.toString()}
          onSelect={(value) => setIsFailed(value)}
          placeholder=""
        />
      </Label>
      <RightSection>
        <ClearButton icon={<CloseIcon />} onClick={handleClear}>
          <Trans value="Rabs.Common.Buttons.Clear"></Trans>
        </ClearButton>
        <SearchButton icon={<SearchIcon />} onClick={handleSearch}>
          <Trans value="Rabs.Common.Buttons.Search"></Trans>
        </SearchButton>
      </RightSection>
    </Wrapper>
  );
};
